import { BondRiskLevel } from '../../types'

export const getRatingTitle = (bondRiskLevel: BondRiskLevel): string => {
    switch (bondRiskLevel) {
        case BondRiskLevel.HIGH:
            return ''
        case BondRiskLevel.MIDDLE:
            return ''
        case BondRiskLevel.LOW:
            return ''
        default:
            return ''
    }
}
